import {
    AppBar,
    Box,
    Button,
    CssBaseline,
    Divider,
    Drawer,
    IconButton,
    List,
    ListItem,
    ListItemButton,
    ListItemText,
    Toolbar,
    Typography
} from "@mui/material";
import MenuIcon from '@mui/icons-material/Menu';
import {Dispatch, FC, ReactNode, SetStateAction, useState} from "react";
import {Link} from "react-router-dom";
import styles from "../styles/styles.module.css";

interface Props {
    window?: () => Window;
    content: ReactNode;
    toggleDarkMode: boolean;
    setToggleDarkMode: Dispatch<SetStateAction<boolean>>;
}

interface NavItem {
    title: string,
    path: string,
}

const navItems: NavItem[] = [
    {
        title: "Home",
        path: "/"
    },
    {
        title: "Teaching",
        path: "/teaching"
    },
]
const drawerWidth = 240;

const NavigationBar: FC<Props> = ({window, content, toggleDarkMode, setToggleDarkMode}) => {
    const [mobileOpen, setMobileOpen] = useState(false);

    const handleDrawerToggle = () => {
        setMobileOpen((prevState) => !prevState);
    };

    const drawer = (
        <Box onClick={handleDrawerToggle} sx={{textAlign: 'center'}}>
            <Typography variant="h6" sx={{my: 2}}>
                Oskar Picus
            </Typography>
            <Divider/>
            <List>
                {navItems.map((item) => (
                    <Link to={item.path} className={styles.link} key={item.title}>
                        <ListItem disablePadding>
                            <ListItemButton sx={{textAlign: 'center'}}>
                                <ListItemText primary={item.title}/>
                            </ListItemButton>
                        </ListItem>
                    </Link>
                ))}
                <ListItem key={""} disablePadding>
                    <ListItemButton
                        sx={{textAlign: 'center'}}
                        onClick={() => setToggleDarkMode((prev) => !prev)}
                    >
                        <ListItemText primary={toggleDarkMode ? "" : ""}/>
                    </ListItemButton>
                </ListItem>
            </List>
        </Box>
    );

    const container = window !== undefined ? () => window().document.body : undefined;

    return (
        <Box sx={{display: 'flex'}}>
            <CssBaseline/>
            <AppBar component="nav">
                <Toolbar>
                    <IconButton
                        color="inherit"
                        aria-label="open drawer"
                        edge="start"
                        onClick={handleDrawerToggle}
                        sx={{mr: 2, display: {sm: 'none'}}}
                    >
                        <MenuIcon/>
                    </IconButton>
                    <Typography
                        variant="h6"
                        component="div"
                        align={"left"}
                        sx={{flexGrow: 1, paddingLeft: "1%", display: {xs: 'none', sm: 'block'}}}
                    >
                        Oskar Picus
                    </Typography>
                    <Box sx={{display: {xs: 'none', sm: 'block'}}}>
                        {navItems.map((item) => (
                            <Link to={item.path} key={item.title}>
                                <Button sx={{color: '#fff'}}>
                                    {item.title}
                                </Button>
                            </Link>
                        ))}
                        <Button
                            key={""}
                            sx={{color: '#fff'}}
                            onClick={() => setToggleDarkMode((prev) => !prev)}
                        >
                            {toggleDarkMode ? "" : ""}
                        </Button>
                    </Box>
                </Toolbar>
            </AppBar>
            <nav>
                <Drawer
                    container={container}
                    variant="temporary"
                    open={mobileOpen}
                    onClose={handleDrawerToggle}
                    ModalProps={{
                        keepMounted: true, // Better open performance on mobile.
                    }}
                    sx={{
                        display: {xs: 'block', sm: 'none'},
                        '& .MuiDrawer-paper': {boxSizing: 'border-box', width: drawerWidth},
                    }}
                >
                    {drawer}
                </Drawer>
            </nav>
            <Box component="main" sx={{p: 3}}>
                <Toolbar/>
                {content}
            </Box>
        </Box>
    );
}

export default NavigationBar;
