import React, {FC} from "react";
import {
    Card,
    CardContent,
    CardContentProps,
    CardProps,
    List,
    ListItem,
    ListItemIcon,
    ListItemText,
    styled,
    Typography
} from "@mui/material";
import CircleIcon from '@mui/icons-material/Circle';

interface Props {
    courseName: string;
    specialisation: string;
    competencies: string[];
}

const CardContentComponent = styled((props: CardContentProps) => {
    return <CardContent {...props} />;
})(({theme}) => ({
    flex: 1
}));

const CardComponent = styled((props: CardProps) => {
    return <Card {...props} />;
})(({theme}) => ({
    minWidth: 200,
    marginRight: theme.spacing(2),
    display: 'flex',
    flexDirection: 'column', // Ensures the content inside Card is displayed in a column
    height: '100%', // Makes sure all cards have the same height in the same row
}));

const CourseCard: FC<Props> = ({courseName, specialisation, competencies}) => {
    return (
        <CardComponent variant={"outlined"}>
            <CardContentComponent>
                <Typography variant="h5" component="div">{courseName}</Typography>
                <Typography sx={{mb: 1.5}} color="text.secondary">
                    {specialisation}
                </Typography>
                <Typography variant="body2">
                    <List>
                        {
                            competencies.map((competency) => (
                                <ListItem key={competency}>
                                    <ListItemIcon>
                                        <CircleIcon/>
                                    </ListItemIcon>
                                    <ListItemText primary={competency}/>
                                </ListItem>
                            ))
                        }
                    </List>
                </Typography>
            </CardContentComponent>
        </CardComponent>
    )
}

export default CourseCard;
